function lockPremiumSettings() {
	const $lockedSettingsContainer = document.querySelector('.peachpay_premium_locked_settings');

	$lockedSettingsContainer
		?.querySelectorAll('input,select,button,textarea,a.pp-button-secondary')
		?.forEach((element: HTMLElement) => {
			element.setAttribute('disabled', 'true');
			if (element.tagName) {
				element.addEventListener('click', event => {
					event.preventDefault();
				});
				(element as HTMLAnchorElement).href = '';
			}
		});

	$lockedSettingsContainer
		?.querySelectorAll('.pp-switch')
		?.forEach((element: HTMLElement) => {
			element.classList.add('pp-switch-disabled');
		});
}

export {
	lockPremiumSettings,
};
