import {handleApplePayDomainRegistration, handleApplePayDomainReset} from './applepay-domain-registration';
import {handleGatewayButtonAjax} from './gateway-ajax';
import {handleStripeCaptureVoidForm} from './stripe/capture-void-payment';
import {gatewayCurrencyAndCountryFilters, hideCustomCountriesAndCurrenciesOptions, insertControlButtons, displaySeeMoreButtons} from './gateway-settings';
import {initIntercom} from './intercom';
import {lockPremiumSettings} from './premium';
import {initNavigationUIState} from './navigation';
import {handlePoyntCaptureVoidForm} from './poynt/capture-void-payment';
import {handleAuthnetCaptureVoidForm} from './authnet/capture-void-payment';
import {handlePoyntWebhooksReset} from './poynt/register-webhook';
import {initUIEvents} from './ui-events';
import {handleDisputeSettingsUi} from './stripe/dispute-setting-ui';
import './deactivation-feedback';
import {relatedProductsToggleUI} from './related-products';
import {botProtectionUI} from './bot-protection';

document.addEventListener('DOMContentLoaded', () => {
	lockPremiumSettings();

	// Admin settings scripts
	initIntercom();
	initUIEvents();
	initNavigationUIState();

	handleGatewayButtonAjax();
	handleApplePayDomainRegistration('square');

	// Gateway settings
	gatewayCurrencyAndCountryFilters();
	hideCustomCountriesAndCurrenciesOptions();
	insertControlButtons();
	displaySeeMoreButtons();

	// Stripe Admin scripts
	handleApplePayDomainRegistration('stripe');
	handleStripeCaptureVoidForm();
	handleDisputeSettingsUi();

	// Poynt admin scripts
	handlePoyntCaptureVoidForm();
	handlePoyntWebhooksReset();

	// Authnet Admin scripts
	handleAuthnetCaptureVoidForm();

	handleApplePayDomainReset();

	// Related Products Settings Page UI
	relatedProductsToggleUI();

	// Bot protection settings page UI
	botProtectionUI();
});

