import {$qs} from '../../../@shared/ts/dom';
import {type IResponse} from '../../../@type/response';

declare const peachpay_admin: PeachPayAdminData;

function handlePoyntWebhooksReset() {
	const $button = $qs<HTMLButtonElement>('#poynt-webhook-register button');
	const $spinner = $qs<HTMLButtonElement>('#poynt-webhook-register img');
	const $error = $qs<HTMLButtonElement>('#poynt-webhook-register span');

	if (!$button || !$spinner || !$error) {
		return;
	}

	const showSpinner = () => {
		$spinner.classList.remove('hide');
		$button.classList.add('hide');
	};

	const hideSpinner = () => {
		$spinner.classList.add('hide');
		$button.classList.remove('hide');
	};

	const clearError = () => {
		$error.innerHTML = '';
	};

	const setError = (errorMessage: string) => {
		$error.innerHTML = errorMessage;
	};

	$button.addEventListener('click', async () => {
		clearError();
		showSpinner();

		const formData = new FormData();

		formData.append('action', 'pp-poynt-register-webhooks');
		formData.append('security', peachpay_admin.nonces.poynt_register_webhooks);

		const response = await fetch(peachpay_admin.admin_ajax, {
			method: 'POST',
			body: formData,
		});

		const data = await response.json() as IResponse<void>;

		if (!data.success) {
			hideSpinner();
			setError(data.message ?? '');
			return;
		}

		setError('Success. Refreshing page...');
		location.reload();
	});
}

export {
	handlePoyntWebhooksReset,
};
